<template>
  <!-- 首页-购买 -->
  <div class="purchase">
    <!-- 轮播图-banner -->
    <div class="hidden-md-and-down">
      <el-carousel height="30rem">
        <el-carousel-item v-for="item in imagesbox" :key="item.id">
          <el-image :src="item.imgurl" class="image" fit="cover"></el-image>
        </el-carousel-item>
      </el-carousel>
      <!-- news-新闻 title="News" -->
      <el-alert type="info" show-icon :closable="false">
        <router-link to='/news/newsDetail'>{{ lastestNews.title }}</router-link>
      </el-alert>
    </div>

    <!-- 购买代币-Purchase Token -->
    <el-row class="purchase-rewards">
      <el-col :lg="12">
        <el-card class="box-card" :body-style="{ padding: '0 0 20px'}">
          <div slot="header" class="clearfix">
            <span class="card-title" style="color: #DAC28F">{{ $t('purchase.card') }}</span>
          </div>
          <div class="card-subhead">
            <span>{{ $t('purchase.subhead') }}</span>
          </div>
          <div class="card-middle">
            <el-row type="flex" justify="space-between">
              <el-col :span="8">
                <el-image class="eth-img" :src="ethUrl" fit="fill"></el-image>
              </el-col>
              <el-col :span="8">
                <el-input
                  :placeholder="$t('purchase.placeholder')"
                  v-model="inputPurchase"
                  type="number"
                  class="input-style"
                />
              </el-col>
              <el-col :span="8">
                <el-image class="yellowkey-img" :src="yellowkeyUrl" fit="fill"></el-image>
              </el-col>
            </el-row>
            <div class="purchase-remind">{{ $t('purchase.remind') }}</div>
          </div>
          <div class="box-btn">
            <el-button round  @click="purchaseBuy()">{{ $t('commom.btnPurchase') }}</el-button>
          </div>
        </el-card>
      </el-col>

      <el-col :lg="12">
        <el-card class="box-card" :body-style="{ padding: '0 0 20px'}">
          <div slot="header" class="clearfix">
            <span class="card-title" style="color: #DAC28F;">{{ $t('purchase.rewards') }}</span>
          </div>
          <div class="card-subhead">
            <el-image class="blackkey-img" :src="blackkeyUrl" fit="fill"></el-image>
          </div>
          <div class="card-middle">
            <el-row type="flex" justify="space-between">
              <el-col :span="8" style="padding-left: 20px">
                <p>{{ reinvestBalance === '--' ? '--' : reinvestBalance.staticBalance }}</p>
                <P>ETH</P>
              </el-col>
              <el-col :span="8" style="border-left: 1px solid #F0DBC5;border-right: 1px solid #F0DBC5;">
                <el-image class="play-img" :src="playUrl" fit="fill"></el-image>
              </el-col>
              <el-col :span="8" style=" padding-right: 20px;">
                <p>{{ reinvestBalance === '--' ? '--' : reinvestBalance.kocBalance }}</p>
                <P>KOC</P>
              </el-col>
            </el-row>
            <div class="middleborder"></div>
          </div>
          <div class="box-btn">
            <el-button round  @click="purchaseReinvest" :disabled="reinvestBalance.staticBalance > 0 ? false : true">{{ $t('commom.btnReinvest') }}</el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-card class="box-card purchase-rule">
      <el-row>
        <p style="margin: 0">{{ $t('purchase.rule1') }}</p>
        <p style="margin: 0">{{ $t('purchase.rule2') }}</p>
      </el-row>
    </el-card>

    <!-- 最近的购买订单-Recent buy order -->
    <el-card class="buyorder-card form-card bottom-card" :body-style="{ padding: '0px' }">
      <div slot="header" class="clearfix">
        <span class="form-title">{{ $t('purchase.buyOrder') }}</span>
      </div>
      <div>
        <el-table :data="recentOrders" style="width: 100%" height="280" size="mini">
          <el-table-column
            prop="date"
            sortable
            :label="$t('commom.date')"
            min-width="22%"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="address"
            :label="$t('commom.address')"
            min-width="20%"
            show-overflow-tooltip
            align="center"
          ></el-table-column>
          <el-table-column
            prop="tx"
            :label="$t('commom.tx')"
            min-width="20%"
            show-overflow-tooltip
            align="center"
          ></el-table-column>
          <el-table-column prop="eth" label="ETH" sortable min-width="20%" align="center"></el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
import ethUtils from '../utils/ethUtils'
import { checkUrlParam } from '../Mixin/checkUrlParam'
import list from '../static/news'
import store from '../store/index'
export default {
  name: 'Purchase',
  mixins: [checkUrlParam],
  data () {
    return {
      ethUrl: require('../assets/img/ETH.png'),
      yellowkeyUrl: require('../assets/img/yellowkey.png'),
      blackkeyUrl: require('../assets/img/blackkey.png'),
      playUrl: require('../assets/img/play.png'),
      imagesbox: [
        { id: 0, imgurl: require('../assets/img/purchase1.jpg') },
        { id: 1, imgurl: require('../assets/img/purchase2.jpg') },
        { id: 2, imgurl: require('../assets/img/purchase3.jpg') },
        { id: 3, imgurl: require('../assets/img/purchase4.jpg') },
        { id: 4, imgurl: require('../assets/img/purchase5.jpg') },
        { id: 5, imgurl: require('../assets/img/purchase6.jpg') },
        { id: 6, imgurl: require('../assets/img/purchase7.jpg') }
      ],
      inputPurchase: 0,
      lastestNews: list[0],
      setIntervalId: ''
    }
  },
  computed: {
    recentOrders () { // todo 页面已经注入
      const recentBuyOrders = this.$store.state.contract.buy
      return recentBuyOrders
    },
    reinvestBalance () {
      let userBalance = this.$store.state.contract.userBalance
      if (userBalance) {
        const staticBalance = userBalance.staticBalance
        const kocBalance = ethUtils.numFloatLen(ethUtils.ethToKoC(staticBalance), 4)
        return {
          staticBalance,
          kocBalance
        }
      } else {
        return '--'
      }
    },
    commitMark: function () {
      return this.$store.state.metamask.commitMark
    }
  },
  watch: {
    commitMark: function (newVal) {
      // 因为purchase页面的reinvest操作，将withdraw页面数据获取，转移至purchase
      const referralAddress = this.checkUrlParam()
      if (!referralAddress) {
        // alert('通过邀请链接的才具有参与资格')
        return
      }
      const status = this.$store.state.metamask.status
      if (status === 1) { // 没有配合事件订阅，每次进入组件重新获取数据
        console.log('开始dispatch获取getUserBalance在purchase页面')
        this.$store.dispatch('contract/getUserBalance')
        if (this.setIntervalId === '') {
          const balanceInterval = this.$ethConf.balanceInterval
          this.setIntervalId = setInterval(function () {
            console.log('执行定时器==================12324335634564')
            store.dispatch('contract/getUserBalance')
          }, balanceInterval) // 3秒更新一次余额
        }
      }
    }
  },
  methods: {
    async purchaseBuy () { // purchase页面 purchase操作，buy接口 todo 没有使用hex进制, 需要测试
      const data = {} // 交易数据
      data.referralAddress = this.checkUrlParam()
      if (!data.referralAddress) {
        // alert('通过邀请链接的才具有参与资格')
        this.$notify({
          title: this.$t('commom.warning'),
          message: '通过邀请链接的才具有参与资格',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      // 非正过滤
      data.value = this.inputPurchase
      this.inputPurchase = 0 // 获取输入框中的值后清空输入框
      if (data.value <= 0) {
        // alert('请输入有效金额...')
        this.$notify({
          title: this.$t('commom.warning'),
          message: '请输入有效金额...',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      // 精度过滤
      const decimalLimit = this.$ethConf.decimalLimit
      const len = data.value.toString().split('.').length
      if (len === 2) {
        if (data.value.toString().split('.')[1].length > decimalLimit) {
          // alert('交易精度过低，请输入有效数据')
          this.$notify({
            title: this.$t('commom.warning'),
            message: '交易精度过低，请输入有效数据',
            position: 'top-left',
            type: 'warning'
          })
          return
        }
      }
      const status = this.$store.state.metamask.status
      if (status !== 1) {
        // alert('请登陆钱包')
        this.$notify({
          title: this.$t('commom.warning'),
          message: '请登陆钱包',
          type: 'warning'
        })
      } else {
        const myProvider = this.$myProvider // todo 使用myProvider，测试通过的话，可以删除 vuex networkVersion
        const networkVersion = myProvider.networkVersion
        if (networkVersion !== this.$ethConf.networkVersion) { // 过滤，非指定网络交易
          // alert('请将metamask网络切换至主网络')
          this.$notify({
            title: this.$t('commom.warning'),
            message: '请将metamask网络切换至主网络',
            type: 'warning'
          })
        } else {
          const contractRes = this.$myContracts && this.$myContracts.contractRes // buy接口调用 Resonance合约地址 todo 标记 引用合约
          const myWeb3 = this.$myWeb3
          data.gasPrice = await myWeb3.eth.getGasPrice()
          data.gasLimit = this.$ethConf.gasLimit
          if (contractRes) {
            data.from = myProvider.selectedAddress
            console.warn('purchase页面合约调用的参数==========', data)
            contractRes.methods.buy(data.referralAddress).send({
              from: data.from,
              gasPrice: data.gasPrice,
              gas: data.gasLimit,
              value: ethUtils.ethToWei(data.value)
            }, function (error, result) {
              if (error) {
                console.error('purchase交易发生错误', error)
              } else {
                console.warn('purchase交易返回直', result) // 交易hash
                // 更新静态复投的数据
                store.dispatch('contract/getUserBalance')
              }
            })
          }
        }
      }
    },
    async purchaseReinvest () { // purchase.vue的reinvest操作
      const referralAddress = this.checkUrlParam()
      if (!referralAddress) {
        // alert('通过邀请链接的才具有参与资格')
        this.$notify({
          title: '警告',
          message: '通过邀请链接的才具有参与资格',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      const data = {} // statistics reinvest type：1
      data.value = 1
      data.amount = this.reinvestBalance.staticBalance
      if (data.amount === '--') {
        this.$notify({
          title: '警告',
          message: '可复投金额正在加载，请稍后重试...',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      if (data.amount <= 0) {
        this.$notify({
          title: '警告',
          message: '已全部复投，不可在复投',
          position: 'top-left',
          type: 'warning'
        })
        return
      }

      const status = this.$store.state.metamask.status
      if (status !== 1) {
        // alert('请登录钱包')
        this.$notify({
          title: '警告',
          message: '请登陆钱包',
          type: 'warning'
        })
      } else {
        const myProvider = this.$myProvider
        const networkVersion = myProvider.networkVersion
        if (networkVersion !== this.$ethConf.networkVersion) {
          // alert('请将metamask网络切换至主网络')
          this.$notify({
            title: '警告',
            message: '请将metamask网络切换至主网络',
            type: 'warning'
          })
        } else {
          const contractRes = this.$myContracts && this.$myContracts.contractRes // Resonance合约地址  todo 标记 引用合约
          const myWeb3 = this.$myWeb3
          data.gasPrice = await myWeb3.eth.getGasPrice()
          data.gasLimit = this.$ethConf.gasLimit
          if (contractRes) {
            data.from = myProvider.selectedAddress
            console.warn('purchase页面合约调用reinvest的参数==========', data)
            const amountHex = myWeb3.utils.toHex(ethUtils.ethToWei(data.amount)) // 统一使用十六进制,对于uint256 solidity
            const valueHex = myWeb3.utils.toHex(data.value)
            contractRes.methods.reinvest(amountHex, valueHex).send({
              from: data.from,
              gasPrice: data.gasPrice,
              gas: data.gasLimit
            }, function (error, result) {
              if (error) {
                console.error('purchase页面reinvest交易发生错误', error)
              } else {
                console.warn('purchase页面reinves交易返回直', result) // 返回交易hash
                // todo 限制再次复投，或者再次获取getUserBalance
                store.dispatch('contract/getUserBalance')
              }
            })
          }
        }
      }
    }
  },
  created () {
    // todo start contract event listen
    const myProvider = this.$myProvider
    if (!this.$ethConf.subscribeMark.Buy && myProvider) {
      if (myProvider.networkVersion === this.$ethConf.networkVersion) {
        const contractRes = this.$myContracts && this.$myContracts.contractRes
        if (contractRes) {
          console.log('purchase页面开始提交监听事件')
          // 组件切换可能会重复订阅。。。 todo 到时测一下  可以使用全局标志，解决
          this.$store.dispatch('contract/startListenEvent', { contract: contractRes, eventName: 'Buy' })
        }
      }
    }
    // 因为purchase页面的reinvest操作，将withdraw页面数据获取，转移至purchase 计算属性具有缓存机制，还有mark的触发问题
    const referralAddress = this.checkUrlParam()
    if (!referralAddress) {
      // alert('通过邀请链接的才具有参与资格')
      return
    }
    const status = this.$store.state.metamask.status
    if (status === 1) { // 没有配合事件订阅，每次进入组件重新获取数据
      console.log('开始dispatch获取getUserBalance在purchase页面')
      this.$store.dispatch('contract/getUserBalance') // 先获取一次，然后开启定时器
      if (this.setIntervalId === '') {
        const balanceInterval = this.$ethConf.balanceInterval
        this.setIntervalId = setInterval(function () {
          store.dispatch('contract/getUserBalance')
        }, balanceInterval) // 3秒更新一次余额
      }
    }
  },
  beforeDestroy () {
    // 清空定时器
    clearInterval(this.setIntervalId)
    this.setIntervalId = ''
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/variables.scss";
.purchase {
  .el-alert {
    color: #909399;
    background: linear-gradient(to bottom, #2d2d2d, #040101);
    border-radius: 0px;
  }
  .purchase-rewards {
      .card-middle {
        height: 110px;
        text-align: center;
        padding: 20px 0;
        .el-col:nth-child(2n) {
          padding-left: 0;
        }
        .el-col:nth-child(2n + 1) {
          padding-right: 0;
        }
        .eth-img {
          width: 50px;
          height: 80px;
        }
        .yellowkey-img {
          width: 90px;
          height: 50px;
          padding-top: 10px;
        }
        i {
          font-size: 70px;
        }
        .input-style {
          padding: 15px 0;
          text-align: center;
        }
        .purchase-remind {
          font-family: ArialMT;
          font-weight: 400;
          font-style: italic;
          color: #ddc597;
          height: 30px;
          line-height: 30px;
          font-size: 18px;
        }
    }
    .blackkey-img {
      width: 72px;
      height: 40px;
      padding: 12.57px 0;
    }
    .play-img {
      width: 65px;
      height: 65px;
    }
  }
  .purchase-rule {
    color: $card-subhead;
  }
}
.box-btn {
  text-align: center;
}
.middleborder {
  margin: 0 auto 20px;
  width: 80%;
  border-bottom: 1px solid #F0DBC5;
}
</style>
<style scoped>
.el-card >>> .el-card__header {
  padding: 8px 20px;
  background: linear-gradient(to bottom, #2c2c2c, #242121);
  font-size: 16px;
  border: 0px solid #282626;
}
</style>



